import React, { Component } from 'react';
import './App.css'
import QuoteBox from './QuoteBox';
import { data } from './quotesData.json'

class QuoteGenerator extends Component {
  constructor(){
    super();
    this.state = {
      quotes: data,
      currentIndex: Math.floor(Math.random()*20),
      currentQuote: '"A notable and historic jumble of words"',
      currentAuthor: 'Someone cool who said words'
    }
    this.changeQuote = this.changeQuote.bind(this);
  }
  changeQuote() {
    this.setState({
      currentIndex: Math.floor(Math.random()*20),
      currentQuote: this.state.quotes[this.state.currentIndex].quote,
      currentAuthor: this.state.quotes[this.state.currentIndex].author
    })}
  render() {
    const quote=this.state.currentQuote
    const author=this.state.currentAuthor
    const changeQuote=this.changeQuote
    return(
      <QuoteBox
        changeQuote={changeQuote} 
        quote={quote} 
        author={author}
      />
    )
  }
}

export default QuoteGenerator;
