import React from 'react'
import logo from './logo.svg';

class QuoteBox extends React.Component {
    render(props){
    const tweetText = 'https://twitter.com/intent/tweet?text="' + this.props.quote + '"%20--' + this.props.author
    const mainContent = 
        <div id='quote-box' className='container-fluid text-center'>
          <div id="text">
            {this.props.quote}
          </div>
            <br></br><br></br>
          <div id="author">
            &mdash;{this.props.author}
          </div>
          <button id='new-quote' onClick={this.props.changeQuote}>New Quote</button>
          <a id='tweet-quote' rel="noopener noreferrer" target="_blank" href={tweetText} data-size="large"><i className='fa fa-twitter'></i>Tweet this!</a>
        </div>
    return (
      <div className='App'>
        <img src={logo} className='App-logo' alt="logo"/>
        {mainContent}
      </div>
    )
    }
}

export default QuoteBox